body {
  text-align: center;
  background-color: black;
  color: white; }
  body button {
    background: -webkit-gradient(linear, left top, right top, from(#1F2443));
    background: -webkit-linear-gradient(left, #1F2443);
    background: -o-linear-gradient(left, #1F2443);
    background: linear-gradient(to right, #1F2443);
    border: 2px groove white;
    color: #FF008D;
    margin: -80px 5px 20px 10px;
    border-radius: 5px;
    padding: 10px 20px;
    font-size: 18px;
    font-weight: bold;
    cursor: pointer; }
  body p {
    line-height: 2px; }

h1 {
  color: #619bc7; }

.board {
  width: 1115px;
  height: 610px;
  margin: 0px auto;
  padding-top: 0px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  background-image: url("https://ak9.picdn.net/shutterstock/videos/4740866/thumb/1.jpg");
  background-repeat: repeat;
  border: 2px white inset;
  border-radius: 15px;
  text-align: center; }

.counter {
  display: block;
  background-color: black;
  color: #619bc7;
  width: 125px;
  margin: 0px auto -20px auto;
  padding-top: 1px;
  border-radius: 5px;
  font-size: 18px;
  line-height: 90px; }
  .counter h1 {
    padding: 0px;
    margin-top: -30px;
    font-size: 58px; }

.cell {
  width: 33px;
  height: 33px;
  border: 2px solid transparent;
  background-color: transparent; }

.alive {
  background: -webkit-gradient(linear, left top, right top, from(#619bc7), to(#1F2443));
  background: -webkit-linear-gradient(left, #619bc7, #1F2443);
  background: -o-linear-gradient(left, #619bc7, #1F2443);
  background: linear-gradient(to right, #619bc7, #1F2443);
  border: 1px groove white;
  border-radius: 19px 25px 10px 25px; }

.nucleus {
  margin: 10px;
  width: 10px;
  height: 10px;
  font-size: 24px;
  -webkit-animation: spin 10s infinite linear;
  color: #FF008D;
  font-weight: bold; }

@-webkit-keyframes spin {
  0% {
    -webkit-transform: rotate(360deg); }
  100% {
    -webkit-transform: rotate(0deg); } }



